import React from 'react'
import '../../styles/pages/About.css';

import CherOfTheMonth from "../../assets/images/chef-of-the-month.jpg";

function About() {
    return (
    <section id="About">
            <div className="text-center">
                <div style={{ fontFamily: 'Oswald, sans-serif' }}>
                    <h4>- Conocenos -</h4>
                    <img src={CherOfTheMonth} className="rounded-staff m-5 mx-auto" alt="logo" height="360" width="600" />
                    <h4>Nuestro Staff</h4>
                    <p>
                        Nos especializamos en creacin de productos gourmet,
                        donde la prioridad esta puesta en la calidad como en la eleccin de los mejores productos frescos y naturales,
                        sin agregado de conservantes ni colorantes.
                        Nuestro chef desarroll una receta de hamburguesas gourmet de estilo casero,
                        llenas de sabor, pan de elaboracin propia y salsas especiales.
                        En <span className="badge text-bg-warning">GOAT BURGER HAUS</span> estamos en esos pequeos detalles,
                        brindndote una clida experiencia para que te sientas comiendo en casa.
                    </p>
                </div>
            </div>
    </section>
    )
}

export default About
